<template>
  <div class="around mt-5">
    <div class="content p-5 mx-auto">
      <div class="banner"></div>
      <div class="text-left mt-5">
        <div class="title-text text-left">特殊任務</div>
        <div class="d-flex flex-wrap flex-md-nowrap">
          <div class="w-half mr-1 mt-1 border p-3 position-relative" v-for="(item, index) in whereList" :key="index" @click="$router.push({name:'Arounddetail',query:{id:item.title, type: 'task'}})">
            <div>
              <p>{{ item.title }}</p>
              <p>{{ item.day }}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="thenewest text-left mt-5">
        <div class="title-text mb-3">集點店家</div>
        <div class="py-3 d-flex border text-white">
          <div class="p-2 text-black prev-btn btn-w bg-gray">last</div>
          <swiper
            ref="swiperComponent"
            :options="swiperOptions" class="d-none d-sm-block">
            <swiper-slide v-for="(item, index) in newestList" :key="index" style="width: 32% !important">
              <div class="p-3">
                <p>the newest</p>
                <p>{{ item.title }}</p>
                <p>{{ item.date }}</p>
                <p>{{ item.date }}</p>
              </div>
            </swiper-slide>
          </swiper>
          <swiper
            ref="swiperComponent"
            :options="swiperOptions" class="d-sm-none">
            <swiper-slide v-for="(item, index) in newestList" :key="index" style="width: 100% !important">
              <div class="p-3">
                <p>the newest</p>
                <p>{{ item.title }}</p>
                <p>{{ item.date }}</p>
                <p>{{ item.date }}</p>
              </div>
            </swiper-slide>
          </swiper>
          <div class="p-2 text-black next-btn btn-w bg-gray">next</div>
        </div>
        <p @click="$router.push({name: 'Store'})">more</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'around',
  data () {
    return {
      whereList: [
        { title: '1111', day: '11/2', content: 'whereList' },
        { title: '2222', day: '11/2', content: 'whereList' },
        { title: '3333', day: '11/2', content: 'whereList' }
      ],
      newestList: [
        { title: '1111', date: '11/2', content: 'newestList' },
        { title: '222', date: '11/2', content: 'newestList' },
        { title: '333', date: '11/2', content: 'newestList' },
        { title: '444', date: '11/2', content: 'newestList' },
        { title: '555', date: '11/2', content: 'newestList' },
        { title: '666', date: '11/2', content: 'newestList' },
        { title: '777', date: '11/2', content: 'newestList' }
      ],
      swiperOptions: {
        // autoplay: {
        //   delay: 3000,
        //   disableOnInteraction: false
        // },
        loop: true,
        slidesPerView: 'auto',
        // loopedSlides: 3,
        loopAdditionalSlides: 3,
        centeredSlides: true,
        spaceBetween: 25,
        // observer: true,
        // observeParents: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.next-btn',
          prevEl: '.prev-btn'
        }
      }
    }
  },
  methods: {
    lastpage () {
      this.$router.back()
    }
  }
}
</script>

<style>

</style>



